:root {
    --primary: #A33B20;
    --secondary: #081318;
    --tertiary: #1C0F13;
    --quatrinary: #E57A44;
    --light: #F5F0F6;
    --dark: #dde0e0;
    --error: rgb(228, 46, 1);
    --border-radius: 0.3rem;
    --spacing-one: calc(1rem * 0.25);
    --spacing-two: calc(1rem * 0.5);
    --spacing-three: 1rem;
    --spacing-four: calc(1rem * 1.5);
    --spacing-five: calc(1rem * 3);
  }
  
  * {
    box-sizing: border-box;
  }
  
  html,
  body {
  
    min-height: 100%;
  }
  
  body {
    margin: 0 0 25% 0;
    padding: 0;
    font-family: 'Inconsolata', monospace;
    font-weight: 400;
    line-height: 1.5;
    font-size: 20px;
    color: var(--light);
    background-color: var(--dark);
    width: 100%
  }
  
  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    font-family: 'Karla', sans-serif;
    font-weight: 700;
    margin: 5px 0;
    line-height: 1.25;
  }
  
  h1 {
    font-size: 2.25rem;
  }
  
  h2 {
    font-size: 2rem;
  }
  
  h3 {
    font-size: 1.75rem;
  }
  
  h4 {
    font-size: 1.5rem;
  }
  
  h5 {
    font-size: 1.25rem;
  }
  
  h6 {
    font-size: 1rem;
  }
  
  p {
    font-size: 1.1rem;
    margin-top: 0;
  }
  
  a {
    color: var(--dark);
    text-decoration: none;
  }
  
  a:visited {
    color: inherit;
  }
  
  nav {
    width: 100%;
    justify-content: center;
  }
  
  nav a {
    padding: 6px 10px;
    margin: 0 15px;
    transition: all 0.2s;
  }
  
  nav a:hover {
    color: var(--secondary);
    background-color: var(--dark);
  }
  
  .page-title {
    text-align: center;
    width: 100%;
    margin: 0;
    font-size: 2.5rem;
  }
  
  .btn,
  .pill {
    font-size: 1.3rem;
    text-decoration: none;
    padding: 0.5rem 1.2rem;
    margin: 5px 0;
    border-radius: var(--border-radius);
    border: none;
    color: var(--dark);
    outline: none;
    background-color: var(--secondary
    );
    transition: all 0.5s;
  }
  
  .btn:hover,
  .pill:hover {
    color: var(--secondary);
    background-color: var(--primary);
  }
  
  .btn-inline {
    display: inline;
  }
  
